import './main.scss';
import BodyTop from '../containers/BodyTop/BodyTop';
import PizzaCard from '../components/PizzaCard/PizzaCard';


const Home = ({ items}) => {
  

   return(
      <div className="home">
         <div className="home-slider">
            <BodyTop onClick={(el) => console.log(el)}
             element={['Мясные', 'Вегетарианская', 'Гриль', 'Острые', 'Закрытые']}
             drop={[
                { name:'популярности', type: 'popular'},
                { name:'по цене', type: 'price' },
                { name:'по алфавиту', type: 'alphabet'}]}/>
         </div>

         <div className="home-body">
           {
             items.map((item) => (
                <PizzaCard
                  key={item.id}
                  {...item}
                  
                />
             ))
            
            }
         </div>
      </div>
   )
}

export default Home;