import './BodyTop.scss';
import {Arrow} from '../../assets/Icons/Icons';
import { useEffect, useRef, useState } from 'react';

const BodyTop = ({ element, drop, onClick}) => {
   const [activeItem, setActiveItem] = useState(null)
   const [activeDropItem, setActiveDropItem] = useState(0)
   const sortRef = useRef()
   const activeLabel = drop[activeDropItem].name

   const onSelectItem = (index) => {
      setActiveItem(index)
   }

   const onSelectDropItem = (index) => {
      setActiveDropItem(index)
   }

   const [visiblePopap, setVisiblePopap] = useState(false)

   const toggleVisiblePopap = () => {
      setVisiblePopap(!visiblePopap)
   }

   const handleOutsideClick = (e) => {
      if(!e.path.includes(sortRef.current)) {
         setVisiblePopap(false)

         
      }
   }

   useEffect(() => {
      document.body.addEventListener('click',  handleOutsideClick)

   }, [])


   

  
   return(
      <div className="body-sidebar">
         <div className="body-sidebar-left">
            <span className={activeItem === null ? "active" : ""} onClick={() => onSelectItem(null)}>Все</span>
           {
               element.map((el, index) => (
                  <span className={activeItem === index ? "active" : ""} onClick={() => onSelectItem(index)} key={`${el}_${index}`}>{el}</span>
              ))
           }
         </div>

         <div className="body-sidebar-right">
            <span><Arrow />Сортировка по:</span>
            <div ref={sortRef} className="drop">
               <h4 onClick={toggleVisiblePopap}>{activeLabel}</h4>
               <div   className={`sort ${visiblePopap ? 'show' : ''}`}>
                  {
                     drop.map((elem, index) => (
                        <h5 className={activeDropItem === index ? "active" : ""} onClick={() => onSelectDropItem(index)} key={`${elem.type}_${index}`}>{elem.name}</h5>
                     ) )
                  }
               </div>
           </div>
           
         </div>
      </div>
   )
}

export default BodyTop;