import './Header.scss';
import Logo from '../../assets/images/logo.png';
import {Basket} from '../../assets/Icons/Icons';

const Header = () => {
   return(
      <div className="header">
         <div className="header-left">
            <img src={Logo} alt="" className="header-left__img" />
            <div className="header-left-item">
               <h1 className="header-left-item__title">
                  REACT PIZZA
               </h1>
               <span className="header-left-item__info">
                  самая вкусная пицца во вселенной
               </span>
            </div>
         </div>

         <div className="header-right">
            <span className="header-right__totalPrice">
               20$
            </span>
            <button className="header-right__basketBtn">
               <Basket/>
               
            </button>
         </div>
      </div>
   )
}

export default Header;