import './PizzaCard.scss'; 
import {Plus} from '../../assets/Icons/Icons';
import { useState } from 'react';
import classNames from 'classnames'


const PizzaCard = ({ imageUrl, name,  price, types, sizes}) => {
   const aviableTypes = ['тонкое', 'традиционное'];
   const aviableSizes = [26, 30, 40];

   const [activeType, setActiveType] = useState(types[0])
   const [activeSize, setActiveSize] = useState(sizes[0])


   const onSelectType = (index) =>{
      setActiveType(index)
   }
   const onSelectSize = (index) =>{
      setActiveSize(index)
   }
   

   const [num, setNum] = useState(null)


   const numPluser = () => {
      setNum(num + 1)
   }

  

   
   return (
      <div className="pizzaCard">
         <div className="pizzaCard-img">
            <img src={imageUrl} alt="" />
         </div>

         <h3 className="pizzaCard__title">
            {name}
         </h3>

         <div className="pizzaCard-info">
            <div className="pizzaCard-info-row">
              {
                  aviableTypes.map((type, index) => (
                    <span 
                     key={type} 
                     onClick={() => onSelectType(index)} 
                     className={classNames({
                        active : activeType === index,
                        disabled : !types.includes(index)
                     })}>
                     {type}</span>
                 ))
              }
            </div>

            <div className="pizzaCard-info-row">
               {
                  aviableSizes.map((size, index) => (
                     <span
                        key={size}
                        onClick={() => onSelectSize(index)}
                        className={classNames({
                           active: activeSize === index,
                           disabled: !sizes.includes(size)
                        })}>
                        {size} см.</span>
                  ))
               }
            </div>
         </div>

         <div className="pizzaCard-footer">
            <h2>
               от {price} ₽
            </h2>

            <button onClick={numPluser} className={`${num > 0 ? "active" : ""}`}>
               <Plus/>
                Добавить
                <span>{num}</span>
            </button>
         </div>
      </div>
   )
}

export default PizzaCard;